.contacts {
    position: relative;
    width: 100%;
    max-height: 1444px;
    background-color: $third-background-color;
    @media screen and (max-width: 767px) {
      padding-bottom: 56px;
      min-height: 1444px;
    } 
    @media screen and (min-width: 768px) and (max-width: 1279px) {
      padding-bottom: 79px;
      min-height: 687px;
    } 
    @media screen and (min-width: 1280px) {
      padding-bottom: 85px;
      min-height: 693px;
      max-height: 700px;
      background-image: url(../images/contacts/contacts.png);
      background-color: $third-background-color;
      background-position: top;
      background-repeat: no-repeat;
      background-size: contain;
      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url(../images/contacts/contacts@2x.png);
        max-height: 700px;
      }
      @media screen and (min-device-pixel-ratio: 3),
        screen and (min-resolution: 288dpi),
        screen and (min-resolution: 3dppx) {
        background-image: url(../images/contacts/contacts@3x.png);
        max-height: 700px;
      }
    }
  }
  .contacts .container {
    @media screen and (max-width: 767px) {
      min-height: 1404px;
    }
    @media screen and (min-width: 768px) and (max-width: 1279px) {
      min-height: 603px;
    }
    @media screen and (min-width: 1280px) {
      min-height: 607px;
    }
  }
  
  @media screen and (min-width: 768px) {
    .contacts__list {
      display: flex;
    }
  }
  
  .contacts__wrapper {
    position: absolute;
    top: 0;
    right: 50%;
    transform: translate(50%, -84px);
    @media screen and (min-width: 768px) {
      transform: translate(50%, -78px);
    }
    @media screen and (min-width: 1280px) {
      transform: translate(50%, -102px);
    }
  }
  
  .contacts__list {
    @media screen and (max-width: 767px) {
      margin-bottom: 54px;
    }
    @media screen and (min-width: 768px) and (max-width: 1279px) {
      margin-bottom: 81px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 86px;
    }
  }
  
  .contacts__item {
    background-color: #ffffff;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    @media screen and (max-width: 767px) {
      padding: 43px 20px;
      width: 220px;
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
    @media screen and (min-width: 768px) {
      padding: 50px 31px;
      width: 220px;
      &:not(:last-child) {
        margin-right: 20px;
      }
    }
    @media screen and (min-width: 1280px) {
      padding: 51px 44px 62px 43px;
      width: 330px;
      &:not(:last-child) {
        margin-right: 31px;
      }
    }
  }
  
  .contacts__title {
    display: flex;
    font-family:$primary-text-font;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.86;
    justify-content: center;
    align-items: center;
    margin: 0 0 25px 0;
    width: 80px;
    height: 16px;
    background: #f0d1a5;
    border-radius: 4px;
    font-size: 12px;
    line-height: 1.86;
    text-align: center;
    color: #ffffff;
    &.contacts--green {
      width: 130px;
      height: 16px;
      background: #c2e297;
    }
    @media screen and (min-width: 768px) {
      margin: 0 0 30px 0;
      width: 73px;
      height: 28px;
      border-radius: 6px;
      font-size: 16px;
      &.contacts--green {
        width: 138px;
        height: 28px;
      }
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 32px;
      font-size: 16px;
      line-height: 1.86;
    }
  }
   
  .contacts__city {
    margin-bottom: 10px;
    font-family:$primary-text-font;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.86;
    color: $default-title-color;
    @media screen and (min-width: 768px) {
      margin-bottom: 14px;
      font-size: 14px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 10px;
      font-size: 16px;
  
    }
  }
  
  .contacts__text {
    font-family: $primary-text-font;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.50;
    letter-spacing: 0.04em;
    color: $secondary-text-color;
    padding-bottom: 25px;
    margin: 0 0 25px 0;
    border-bottom: 1px solid #e1e1e1;
  
  
    @media screen and (min-width: 768px) {
      padding-bottom: 30px;
      margin-bottom: 30px;
      font-size: 14px;
      line-height: 1.66;
    }
    @media screen and (min-width: 1280px) {
      padding-bottom: 32px;
      margin-bottom: 32px;
      font-size: 16px;
      line-height: 1.83;
    }
  }
  
  .contacts-table {
    padding-bottom: 25px;
    border-bottom: 1px solid #e1e1e1;
    margin: 0 0 25px 0;
    @media screen and (min-width: 768px) {
      margin-bottom: 30px;
      padding-bottom: 30px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 32px;
      padding-bottom: 32px;
    }
  }
  
  .contacts-table__day {
    margin: 0;
    font-size: 12px;
    line-height: 1.86;
  
    color: $secondary-text-color;
    @media screen and (min-width: 768px) {
      font-size: 14px;
    }
    @media screen and (min-width: 1280px) {
      font-size: 16px;
    }
  }
  
  .contacts-table__time {
    margin: 0;
    font-size: 12px;
    line-height: 1.86;
    color:  #FFA5BA;
    @media screen and (min-width: 768px) {
      font-size: 14px;
    }
    @media screen and (min-width: 1280px) {
      font-size: 16px;
    }
  }
  
  .contacts-table__workday:not(:last-child) {
    margin-bottom: 10px;
    @media screen and (min-width: 768px) {
      margin-bottom: 16px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 15px;
    }
  }
  
  .contacts-contacts {
  
    font-style: normal;
    font-size: 12px;
    line-height: 1.5;
    color: $secondary-text-color;
    a {
      color: inherit;
      text-decoration: none;
    }
    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.83;
    }
    @media screen and (min-width: 1280px) {
      font-size: 16px;
    }
  }
  
  .contacts-button-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    @media screen and (min-width: 768px) {
      flex-direction: row;
      justify-content: center;
    }
    &__item:not(:last-child) {
      margin-bottom: 10px;
      @media screen and (min-width: 768px) {
        margin-bottom: unset;
        &:not(:last-child) {
          margin-right: 15px;
        }
      }
    }
  }
  
  .button-our {
  
    font-family:$primary-text-font ;
    font-style: normal;
    font-weight:700;
    font-size: 16px;
    line-height: 1.31;
    align-items: center;
    text-align: center;
    height: 44px;
    padding-left: 17px;
    padding-right: 17px;
    border: 1px solid #ffffff;
    border-radius: 22px;
    cursor: pointer;
  
    @media screen and (min-width: 768px) {
      padding-left: 30px;
      padding-right: 30px;
    }
    @media screen and (min-width: 1280px) {
      padding-left: 35px;
      padding-right: 35px;
  
    }
  }
  
  .arrow--light{
    stroke: $accent-color;
  }
  .arrow--dark{
    stroke: $default-color;
  }
    
  .button--dark{
    color: $default-color;
    background: $accent-color;
    border: 1px solid #d41443;
    margin-bottom: 10px;
    @media screen and (min-width: 768px) {
      margin-right: 14.5px;
      margin-bottom: 0;
    }
    @media screen and (min-width: 1280px) {
      margin-right: 15px;
      margin-bottom: 0;
    }
  
  }
  
  .button--light{
    color: $accent-color;
    background: $default-color;
    border: 1px solid #ffffff;
  }
  
  .button--dark:hover,
  .button--dark:focus {
    background: #C7133E;
    border: 1px solid #C7133E;
  
  }
  
  .button--light:hover,
  .button--light:focus {
    background: #ECEBEB;
    border: 1px solid #ECEBEB;
  }